body{
  background-color: #8e91c8;
}

.app-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.image-container{
  width:250px;
  margin: 16px;
  background-color: #4d528f;
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
}

.image-container img{
  max-width: 100%;
}

.image-info{
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}

.image-info h4{
  margin: 0;
}

.image-info span{
  margin-left: 3px;
}

.videoWrapper{
  width: 100%;
  height: 30%;
  margin-bottom: 20px;
}

.video{
  height: 90%;
  width: 70%;
  margin-right:15%;
  margin-left: 15%;
}

.title{
  font-size: 60px;
  text-align: center;
  color: aliceblue;
  margin-bottom: 20px;
  padding-top: 10px;
  font-weight: 600;
  font-style: italic;
}